import jss from 'jss';
import jssGlobal from 'jss-plugin-global';
import jssNested from 'jss-plugin-nested';
import { ParameterRadioGroup } from './radio_group';

export function setSingleSelectRadioFontSize(baseCls: string, radioWidth: number) {
    const cls = `${baseCls}-${BI.UUID()}`;

    const selector = `.${cls} .bi-radio .radio-content`;

    jss.use(jssGlobal(), jssNested());

    const afterSize = (radioWidth / 2 - 1) / 4 * 3;

    jss.createStyleSheet({
        '@global': {
            [`${selector}`]: {
                'border-radius': '50%',
            },
            [`${selector}:after`]: {
                width: `${afterSize}px`,
                height: `${afterSize}px`,
                'border-radius': '50%',
            },
        },
    }).attach();

    return cls;
}

export function setRadioGroupStyle({ opacity, background, color }: {
    opacity?: number;
    background?: string;
    color?: string;
}) {
    jss.use(jssGlobal(), jssNested());

    const cls = `${ParameterRadioGroup.BASE_CLS}-${BI.UUID()}`;

    const selector = `.${cls}`;

    jss.createStyleSheet({
        '@global': {
            [`${selector}`]: {
                opacity,
                background,
                color,
            },
        },
    }).attach();

    return cls;
}
